.close-instructions-pane {
  position: absolute;
  top: 1.2rem;
  right: 2rem;
  color: #333;
  cursor: pointer;
  border: none;
  background: none;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(3px);
  z-index: 5;
}

p {
  font-family: arame;
}

span#warning {
  color: red;
}

#socials {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: auto;
  gap: 5px;
}

#linkedin,
#github {
  height: 35px;
  width: 35px;
}